"use client";

import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  type ReactNode,
} from "react";
import { apiFetchClient, getJsonClient, type User } from "./fetcher.client";

type SessionValue = {
  user: User | null;
  loading: boolean;
  refresh: () => Promise<void>;
  signOut: () => Promise<void>;
};

const SessionCtx = createContext<SessionValue | undefined>(undefined);

export function SessionProvider({
  children,
}: {
  children: ReactNode;
}): React.ReactElement {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  const refresh = async () => {
    setLoading(true);
    try {
      const data = await getJsonClient<{ user: User }>("/api/auth/session");
      setUser(data.user ?? null);
    } catch {
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const signOut = async () => {
    try {
      await apiFetchClient("/api/auth/logout", { method: "POST" });
    } catch {}
    await refresh();
  };

  useEffect(() => {
    void refresh();
  }, []);

  return (
    <SessionCtx.Provider value={{ user, loading, refresh, signOut }}>
      {children}
    </SessionCtx.Provider>
  );
}

export function useSession(): SessionValue {
  const ctx = useContext(SessionCtx);
  if (!ctx) throw new Error("useSession must be used within SessionProvider");
  return ctx;
}
